.speechBubble {
    transition: opacity 500ms; /* Only transition the opacity */
    display: inline-block; /* Always keep the display as inline-block, control visibility using opacity */
    opacity: 0; /* Hidden by default using opacity */

    position: absolute;
    top: -10%;
    right: -120%;
    color: black;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
    white-space: nowrap;

    
    &.opaque {
        opacity: 1; /* Show bubble when logo is hovered */
        color: black;
        transition: 500ms;
    }
    
    &.invisible {
        opacity: 0; /* Hide bubble when logo is hovered */
        &.opaque {
        opacity: .7; /* Show bubble when logo is hovered */
        color: black;
        transition: 1000ms;
    }
    }
    &.activeSpeechBubble {
    opacity: 100%;
    transition: 200ms;
    color: black;
    &.invisible {
        opacity: .7; /* Hide bubble when logo is hovered */
    }

}

    

    /* Removed the repeated styles from .opaque, .invisible, and .visible. */
}

.speechBubble:hover {
    
    
    &.opaque {
        opacity: .8; /* Show bubble when logo is hovered */
    }
 
}

.speechBubble::before {
    content: ''; 
    position: absolute;
    top: 35%; /* Position it at the top of the speech bubble */
    right: 90%; /* Shift it a bit to the left */
    border-left: 10px solid transparent; 
    border-right: 0px solid transparent;
    border-bottom: 20px solid #fff; /* This color should match the background of the bubble */
    border-top: -10px solid transparent;
   
}


