    background-color: #0000002b;
    border: solid rgba(0, 0, 0, 0.106) 0.5px;
    pointer-events: all;
    cursor: pointer;
    color: white;
    font-size: calc(.5rem + 1vw);
    padding: 2vh 5vw;
    font-family:'Calibri', sans-serif;

    span {
        cursor: pointer;
        display: inline-block;
        position: relative;
        transition: 500ms;

        &:after {
            content: '\00bb';
            position: absolute;
            opacity: 0;
            top: 0;
            right: -15px;
            transition: 500ms;
        }
    }

    &:hover {
        background-color: #0000007a;
        transition: 200ms;
    }

    &:active {
        color: #aa0a0ade;
        transition: 1ms;
    }

    &:hover span {
        padding-right: 15px;
        font-weight: bolder;

        /* transition: 100ms; */
        /* transition-property: font-weight; */

        &:after {
            opacity: 1;
            right: 0;
        }
    }
}

